* {
  margin: 0;
  padding: 0;
  box-sizing: border-box;
}

html {
  font-family: 'opens sans', Arial, sans-serif;
  font-size: 20px;
}
div.app {
  display: flex;
  width: 90%;
  min-height: 100vh;
  margin: 0  auto;
  flex-direction: column;
  color: #222
}

header {
  flex-basis: 30vh;
}

main {
  padding: 30px 0;
  display: flex;
  flex-grow: 1;
}

aside {
  min-width: 200px;
}

section.page {
  flex-grow: 1;
}

footer {
  background-color: black;
  color: white;
  padding: 10px 0;
  text-align: center;
  text-transform: uppercase;
  margin-bottom: 50px;
}
header {
    position: relative;
    overflow: hidden;
}

header img {
    position: absolute;
    top: 50%;
    left:50%;
    transform: translate(-50%, -50%);
    min-width: 100%;
    min-height: 100%;
}
.article {
    margin-top: 40px;
}

.article h3 {
    margin-bottom: 3px;
    text-transform: uppercase;
}

.article span {
    display: block;
    margin-bottom: 10px;
    font-size: 16px;
}

.article p {
    font-size: 20px;
}
.products h2 {
    margin-bottom: 20px;
}

.products ul {
    list-style: square;
    padding-left: 20px;
}

.products li {
    margin-bottom:  20px;
}

.products a {
    text-decoration: none;
    color: #333;
}

.products a:hover {
    text-decoration: underline;
    color: black;
}
.contact textarea {
    font-size: 16px;
    width: 80%;
    min-height: 200px;
    margin: 20px 0;
    line-height: 1.6;
}

.contact button {
    display: block;
    border: 2px solid black;
    background-color: #fff;
    cursor: pointer;
    padding: 10px 20px;
    margin: 0 20% 0 auto;
    font-size: 20px;
    transition: .2s;
}

.contact button:hover {
    color: yellow;
    background-color: black;
}
footer span {
    color:red;
}

footer p {
    font-size: 14px;
}
nav.main {
    padding: 0 30px 30px 0;
}

.main ul {
    list-style-type: none;
}

nav.main a {
    display: block;
    text-decoration: none;
    color: black;
    text-transform: uppercase;
    font-weight: bold;
    padding: 10px;
    transition: .2s;
}

nav.main a:hover {
    transform: translate(20px);
}

nav.main a.active {
    border-left: solid 10px grey;
    transform: translate(0);
}

nav.main li:nth-child(1) a.active {
    border-left-color: red;
}

nav.main li:nth-child(2) a.active {
    border-left-color: orange;
}

nav.main li:nth-child(3) a.active {
    border-left-color: yellow;
}

nav.main li:nth-child(4) a.active {
    border-left-color: green;
}
